.sevenmodal-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  opacity: .6;
  z-index: 9998;
}


.sevenmodal-wrapper {


}

.sevenmodal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  width: 300px;
  height: 200px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 5px;
  background: #fff;
  overflow: hidden;
  z-index: 9999;
  box-shadow: inset 0 0 1px 0 #000;
}

.sevenmodal-operator {
  width: 100%;
  height: 50px;
}

.sevenmodal-operator-close:active, .sevenmodal-operator-confirm:active {
  opacity: .6;
  transition: opacity .3s;
}
.sevenmodal-operator-close:hover, .sevenmodal-operator-confirm:hover {
   opacity: .6;
  transition: opacity .3s;

}

.sevenmodal-operator-close, .sevenmodal-operator-confirm {
  width: 50%;
  border: none;
  outline: none;
  height: 50px;
  line-height: 50px;
  opacity: 1;
  color: #fff;
  background: rgb(247, 32, 32);
  cursor: pointer;
}

.sevenmodal-title {

  padding: 13px 16px;
  border-radius: 4px 4px 0 0;
  color: #595959;
  border-bottom: 1px solid #d9d9d9;
}

.sevenmodal-content {
  width: 100%;
  padding: 10px 10px;
}


.sevenmodal-enter {
  transform: scale(0);
}

.sevenmodal-enter-active {
  transform: scale(1.1);
  transition: all .2s linear;
}
.sevenmodal-enter-end {
  transform: scale(1);
  transition: all .1s linear;
}



.sevenmodal-leave {
  transform: scale(1);
}

.sevenmodal-leave-active {
  transform: scale(1.1);
  transition: all .1s linear;
}

.sevenmodal-leave-end {
  transform: scale(0);
  transition: all .2s linear;
}


.sevenmodal-mask-enter {
  opacity: -1;
}
.sevenmodal-mask-active {
  opacity: .6;
  transition: opacity .2s ease-in;
}

.sevenmodal-mask-leave {
  opacity: .6;
}

.sevenmodal-mask-leaveactive {
  opacity: -1;
  transition: opacity .2s ease-out;
}


